<template>
  <div class="menuBox">
    <el-menu :default-active="defaultActive" class="el-menu-vertical-demo">
      <el-menu-item
        :index="index.toString()"
        v-for="(item,index) in menuList"
        :key="index"
        @click="$router.push(item.key)"
      >
        <span slot="title">{{item.value}}</span>
      </el-menu-item>
    </el-menu>
  </div>
</template>
<script>
export default {
  data() {
    return {
      defaultActive:"0",
      menuList: [
        {
          key: "myInfo",
          value: "我的信息"
        },
        {
          key: "myClass",
          value: "我的班级"
        },
        {
          key: "employStatistics",
          value: "就业统计"
        },
        {
          key: "careerGuidance",
          value: "就业指导"
        },
        {
          key: "recruitment",
          value: "招聘信息"
        }
      ]
    };
  },
  methods: {
    // handleOpen(){
    //   console.log("aaa")
    // }
  },
  created(){
    // 初始化路由
    let routePath = this.$route.path;
    routePath=routePath.split("/")[1]
    this.menuList.map((item,index) =>{
      if(item.key == routePath){
        this.defaultActive = index.toString();
      }
    })
  }
};
</script>
<style lang="scss" scoped>
.menuBox {
  text-align: center;
  .el-menu {
    border: none;
  }
  // border-right: rgb(133, 200, 255) solid 2px;
}
</style>